import React,{Component} from 'react';
import './css/Registro.css';
import axios from 'axios';

class Registro extends Component{
    nombreRef = React.createRef();
    correoRef = React.createRef();
    passwordRef = React.createRef();
    apellidoRef = React.createRef();
    obtenerDatos=(e)=>{
        e.preventDefault();

        let nombre = this.nombreRef.current.value;
        let correo = this.correoRef.current.value;
        let password = this.passwordRef.current.value;
        let apellido = this.apellidoRef.current.value;


        let data = {nombre,correo,password,apellido};
        console.log(data);
        axios.post('/registro',data).then(res=>console.log(res)).catch(err=>console.log(err))

    }
    render(){
        return(
            <div className="container" id="container-registro">
                <div className="row justify-content-center" >
                    <div className="col-10 col-md-6 text-center" >
                        <h2>Ingresa tus datos</h2>
                        <form className="pb-3" onSubmit={this.obtenerDatos}>
                            <div className="form-group text-left">
                                <label>Nombre</label>
                                <input type="text" className="form-control" ref={this.nombreRef}/>
                            </div>
                            <div className="form-group text-left">
                                <label>Apellido</label>
                                <input type="text" className="form-control" ref={this.apellidoRef}/>
                            </div>
                            <div className="form-group text-left">
                                <label>Correo</label>
                                <input type="email" className="form-control" ref={this.correoRef}/>
                            </div>
                            <div className="form-group text-left">
                                <label>Contraseña</label>
                                <input type="password" className="form-control" ref={this.passwordRef}/>
                            </div>
                            <button type="submit" className="btn btn-info">Registrarme</button>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}
export default Registro;