import React,{Component} from 'react';
import './css/Home.css';
import Login_Registro from './Login_Registro.jsx';
class Home extends Component{
    render(){
        let login = null;
        if(!this.props.logged)login = <Login_Registro/>;
        else login = 'logged';
        return(
            <div id="container-home">
                <div className="container">
                    <div  className="row">
                        <div className="col-12 text-right">
                            {login}
                        </div>
                        <div className="col-12 text-center">
                            <h1>Registre una tarea</h1>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Home;