import React,{Component} from 'react';
import axios from 'axios';
import './css/Login.css';
class Login extends Component{
    state={
        Error:false,
        mensajeError: ''
    }
    correoRef = React.createRef();
    passRef = React.createRef();

    getDatos=e=>{
        
        e.preventDefault();

        const correo = this.correoRef.current.value;
        const pass = this.passRef.current.value;
        const data = {correo,pass};

        axios.put('/login',data).then(res=>{
            console.log(res);
            if(!res.data.ok){
                this.setState({
                    Error:true,
                    mensajeError:res.data.mensaje
                })
            }else{
                this.setState({
                    Error:true,
                    mensajeError:`Benvenido ${res.data.usuario.nombre}`
                });
                this.props.cambiarLogin()
            }
        })
        .catch(err=>console.log(err));
    }
    
    render(){
        let palabra=null;
        if(this.state.Error) palabra = <span id="rojo">{this.state.mensajeError}</span>
        return(
        <div className="container mt-5" id="container-login">
            <div className="row justify-content-center">
                <div className="col-6 text-center">
                    <h2>Ingresa como usuario</h2>
                    <form className="pb-4" onSubmit={this.getDatos}> 
                        <div className="form-group text-left">
                            <label>Correo</label>
                            <input className="form-control" type="email" ref={this.correoRef}/>
                        </div>
                        <div className="form-group text-left">
                            <label>Contraseña</label>
                            <input className="form-control" type="password" ref={this.passRef}/>
                        </div>
                        <button className="btn btn-info w-100" type="submit">Ingresar</button>
                    </form>
                    {palabra}
                </div>

            </div>
        </div>)
    }
}

export default Login;